body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #ffffff;
  text-align: center;
  width: 100vw;
  overflow-x: hidden;
}
h1, h2, h3, h4 {
  cursor: default;
}
p {
  margin: 0;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.img__logo {
  width: 455px;
  height: 187px;
}
.img__info {
  margin-top: 5px;
  border-radius: 7px;
  box-shadow: 2px 0 1px #000000;
}
.flex__break {
  flex-basis: 100%;
  width: 0;
  height: 20px;
  order: 6;
  border-bottom: 2px dashed #000000;
  margin-bottom: 5px;
}
.button__container {
  height: 100%;
  padding: 10px;
  background-color: #e65d0e;
  box-shadow: 0 1px 2px #000000;
}
#info {
  position: absolute;
  top: -43px;
  right: 90%;
  padding: 2px 2px 2px 2px;
  border-radius: 50%;
  cursor: pointer;
  background: transparent;
  border-width: thin;
  outline: #e65d0e;
}
/* #info:hover {
  box-shadow: 0 0 10px #64f864, 0 0 40px #64f864, 0 0 80px #64f864;
} */
.button__click__container {
  position: relative;
  top: 12px;
  left: 90%;
  background-color: #373738;
  width: 60px;
  height: 26px;
  padding: 1px 0 1px 0;
  border-radius: 30px;
  border: 5px solid #ffffff;
}
.button__click {
  position: relative;
  right: 17px;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  cursor: pointer;
  outline: none;
  background: transparent;
  border: 2px solid #91C9FF;
  box-shadow: 2px 3px 1px #000000;
  transition-duration: 0.5s;
}
.button__add {
  width: 180px;
  height: 60px;
  cursor: pointer;
  background: transparent;
  border: 1px solid #91C9FF;
  outline: none;
  transition: 1s ease-in-out;
}
.svg__container {
  position: relative;
}
svg {
  position: absolute;
  left: -6px;
  top: -16px;
  fill: none;
  stroke: #fff;
  stroke-dasharray: 150 480;
  stroke-dashoffset: 150;
  transition: 1s ease-in-out;
}
.button__add:hover {
  transition: 1s ease-in-out;
  background: #008000;
  box-shadow: 0 0 10px #64f864, 0 0 40px #64f864, 0 0 80px #64f864;
  /* background: #4F95DA; */
}
.button__add:hover svg {
  stroke-dashoffset: -480;
}
.button__add span {
  color: white;
  font-size: 20px;
  font-weight: 100;
  font-family: 'Kaushan Script', cursive;
  /* font-family: 'Euphoria Script', cursive; */
  /* font-family: 'Ribeye Marrow', cursive; */
}
.animation--red {
  background-color: rgba(255,0,0,0);
  animation-name: red;
  animation-duration: 1.5s;
  animation-iteration-count: 2;
}
@keyframes red {
  50% {background-color:  rgba(255,0,0,0.4)}
}
#header {
  text-align: center;
}
.container {
  display: flex;
  flex-basis: 1.5em;
  flex-flow: row wrap;
  padding: 5px;
  padding-bottom: 30px;
  margin-bottom: 10px;
  margin-top: 20px;
  align-items: stretch;
  justify-content: center;
  background-color: #e65d0e;
  box-shadow: 0px 1px 2px #000000;
}
.container h1 {
  width: 200px;
  /* overflow: hidden; */
}
.day__container {
  border: 1px black solid;
  background-color: #ffffff;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  flex-grow: 0.1;
  /* transition-duration: 1s; */
}
.day__header {
  padding: 5px;
  min-height: 1.5em;
  min-width: 200px;
  /* max-width: 8em; */
  /* border-bottom: 1px dashed #000000; */
}
.day__header h1 {
  margin: auto;
  padding: 0.5em;
  /* font-family: 'Fredericka the Great', cursive; */
  font-family: 'Poiret One', cursive;
  /* font-family: 'Merienda', cursive; */
  
}
.day__chart {
  background-color: cadetblue;
  /* margin-top: 2px; */
  min-height: 54px;
  flex-grow: 1;
  box-shadow: 2px 2px 2px #000000;
  /* height: 48%; */
}
.day__chart--font {
  font-family: 'Merienda', cursive;
  margin: 0;
  font-size: 110%;
}
.day__data {
  background-color: #008000;
  transition-duration: 1s;
  cursor: crosshair;
  text-align: justify;
  padding: 3px;
  padding-left: 5px;
  border-left: 10px solid #373738;
  box-shadow: 2px 2px 2px #000000;
}
.day__data:hover {
  background-color: #ffffff;
  border-left: 10px solid #ffd900;
}
.day__data p {
  font-family: 'Kaushan Script', cursive;
  font-family: 'Baloo Thambi 2', cursive;
}
.button {
  position: absolute;
  left: 88%;
  bottom: 0.45em;
  cursor: pointer;
  background: transparent;
  border: none;
  outline: none;
  transition: 1s ease-in-out;
}
.for--button {
  position: relative;
}
.modal__day {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  background-color: rgba(0,0,0,0.4);
  z-index: 20;
}
.modal__inside {
  position: absolute;
  left: 25%;
  right: 25%;
  bottom: 25;
  top: 15%;
  margin: auto;
  padding: 2px;
  background-color: #ffffff;
  border: 1px solid #000000;
  z-index: 21;
  animation-name: modal;
  transform: translateY(10px);
  animation-duration: 0.3s;
  opacity: 0%;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
}
@keyframes modal {
  100% {transform: translateY(0); opacity: 100%}
}
.modal__inside h1 {
  /* font-family: 'Ribeye Marrow', cursive; */
  font-family: 'Kaushan Script', cursive;
}
.button__submit {
  /* font-family: 'Kaushan Script', cursive; */
  font-family: 'Baloo Thambi 2', cursive;
  font-size: 1em;
  cursor: pointer;
}
.modal__button {
  float: right;
  margin: 2px;
  margin-top: 4px;
  cursor: pointer;
  padding: 0 0 0 0;
  /* background: transparent; */
  background-color: #000000;
  border: none;
  border-width: thin;
  outline: none;
}
.modal__button:hover {
  background: transparent;
}
.modal__button:active {
  /* background-color: #373738; */
  background-color: #000000;
  
}
.modal__label {
  display: flex;
  align-items: center;
  max-height: 1.1em;
}
.modal__container {
  display: flex;
  padding: 5px;
  flex-flow: row wrap;
  justify-content: stretch;
}
.modal__container ul {
  font-family: 'Baloo Thambi 2', cursive;
  text-align: justify;
  padding: 0 7% 0 7%;
}
.modal__block {
  min-width: 8em;
  /* max-width: 12em; */
  height: 150px;
  padding: 10px;
  margin: 5px;
  display: flex;
  flex-grow: 1;
  flex-direction: column;
  align-items: center;
  border-radius: 10px;
  /* font-family: 'Merienda', cursive; */
  font-family: 'Baloo Thambi 2', cursive;
  font-size: 1.2em;
}
.modal__block textarea {
  font-family: 'Kalam', cursive;
  font-size: 0.9em;
}
.modal__block input {
  font-family: 'Baloo Thambi 2', cursive;
  font-size: 0.9em;
}
.modal__note {
  width: 650px;
}
.info {
  max-height: 20em;
  overflow: auto;
  margin-bottom: 3px;
  cursor: default;
}
.info div {
  padding-top: 10px;
}
#ghost {
  display: none;
  position: absolute;
  padding: 5px;
  min-width: 5em;
  max-width: 20em;
  min-height: 1.5em;
  background-color: #92ffff;
  box-shadow:
  -1px -1px 0 #000,  
   1px -1px 0 #000,
   3px 3px 2px #000,
    1px 1px 0 #000;
  z-index: 100;
  font-family: 'Kalam', cursive;
  font-size: 1.2em;
  /* animation-duration: 0.4s;
  animation-name: ghost;
  opacity: 0%;
  animation-iteration-count: 1;
  animation-fill-mode: forwards; */
}
/* @keyframes ghost {
  100% {opacity: 100%;}
} */
footer {
  font-size: 0.7em;
  color: #ffffff;
  transition-duration: 1.5s;
  cursor: default;
}
footer:hover {
  color: #000000;
}
@media only screen and (max-device-width: 1200px) {
  .modal__note {width: 550px}
}
@media only screen and (max-device-width: 800px) {
  .modal__note {width: 270px}
}
@media only screen and (max-device-width: 500px) {
  .modal__note {width: 150px}
}

/* Custom scrollbar */

::-webkit-scrollbar {
  width: 10px;
}
::-webkit-scrollbar-track {
  background: #f1f1f1; 
}
::-webkit-scrollbar-thumb {
  background: #888; 
}
::-webkit-scrollbar-thumb:hover {
  background: #555; 
}
